const ManifestoContainer = styled.div`
width: 100%;
//max-height: 100svh;
//overflow-y: scroll;
`
//Верхний блок
const ManifestoTopBlock = styled.div`
position: relative;
background-image: url(${gradientImg});
background-size: cover;
background-repeat: no-repeat;
height: 100svh;

a{
    position: absolute;
    top:50%;
    left:50%;
    width: 71%;
    transform:translate(-50%, -50%); 

    font-size: 4dvw;
    text-align: center;
    font-weight: 600;

    @media (max-width: 600px){
        font-size: 36px;
        text-align: left;
        width: 79%;
    }
    @media (max-width: 360px){
        font-size: 32px;
      }
}

img{
    position: absolute;
    top: 76%;
    left: 95%;
    width: 2%;

}
`
//контент
const ManifestContent = styled.div`
//position: relative;
display: flex;
flex-direction: column;
align-items: center;
background-color: rgba(30, 30, 30, 1);

grid-template-rows: 200dvw;
`

//Большой заголовок
const ManifestBigHeder = styled(FadeInComponent)`
margin: 0;
margin-top: -2.2svw;
margin-bottom: 4.2svh;
width: 100%;
font-size: 10dvw;
text-align: center;
font-weight: 900;

@media (max-width: 600px){
    font-size: 14svw;
    margin-top: 2.7svh;
    margin-bottom: 4svh;
}
`
//Элемент картинки на всю ширину экрана
const FullWidthImg = styled(FadeInComponent)`
object-fit: cover;
width: 100%;

@media (max-width: 600px){
    height: 45svh;
    object-fit: cover;
    object-position: 35% 0;
    overflow: hidden;
}
`

const ColumnsBlock = styled(FadeInComponent)`
width: 95%;
display: flex;
margin-top: 4.2svh;
margin-bottom: 8svh;

@media (max-width: 600px){
    flex-direction: column;
}
`
//Заголовок поменьше
const ColumnsBlockHeader = styled.h2`
    margin: 0;
    grid-column-start: 1;
    grid-column-end: 3;
    font-size: 36px;
    font-weight: 400;
    flex-grow: 1;
    max-width: 40%;

    @media (max-width: 1600px){
        font-size: 32px;
      }

    @media (max-width: 1400px){
        font-size: 28px;
      }

    @media (max-width: 600px){
      font-size: 16px;
      max-width: 100%;
      padding-left: 3%;
    }

    @media (max-width: 360px){
        font-size: 24px;
      }
`

const ColumnsTextStyle = css`
    font-size: 18px;
    font-weight: 300;

    @media (max-width: 1600px){
        font-size: 16px;
      }

    @media (max-width: 1400px){
        font-size: 14px;
      }

    @media (max-width: 600px){
      font-size: 16px;
    }

    @media (max-width: 360px){
        font-size: 14px;
      }
`
const TwoColumnsText = styled.div`
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-right: 5px;
    ${ColumnsTextStyle}

    a{
        display: block;
        width: 94%;
    }

    @media (max-width: 800px){
        width: 60%
        
    }

    @media (max-width: 600px){
    width: 100%;
    }

`
const OneColumnText = styled.div`
    ${ColumnsTextStyle}
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    a{
        display: block;
        width: 94%;
    }

    @media (max-width: 800px){
        width: 30%;
    }

    @media (max-width: 600px){
    width: 100%;
    }
`

